.toggle {
  display: flex;
  align-items: center;
  margin: 0;
  cursor: pointer; }
  .toggle input {
    margin: 8px 16px 8px 0; }

@supports (--css: variables) {
  [type=checkbox],
  [type=radio] {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    border-radius: 0;
    background: none;
    appearance: none;
    --color: blue; }
    [type=checkbox]:before,
    [type=radio]:before {
      content: '';
      display: block;
      transition: width 200ms, height 200ms, border-color 200ms, transform 200ms, opacity 200ms; }
    [type=checkbox]:checked,
    [type=radio]:checked {
      --color: blue; }
    [type=checkbox][disabled], fieldset[disabled] [type=checkbox],
    [type=radio][disabled], fieldset[disabled]
    [type=radio] {
      --color: #505050; }
      [type=checkbox][disabled] + span, fieldset[disabled] [type=checkbox] + span,
      [type=radio][disabled] + span, fieldset[disabled]
      [type=radio] + span {
        color: #505050; }
  [type=checkbox] {
    border: 0; }
    [type=checkbox]:before {
      width: 100%;
      height: 100%;
      transform-origin: 100% 50%;
      border: 1px solid var(--color); }
    [type=checkbox]:checked:before {
      width: 50%;
      transform: rotate(45deg);
      border-top-color: transparent;
      border-left-color: transparent; }
  [type=radio] {
    border: 1px solid var(--color);
    border-radius: 50%; }
    [type=radio]:before {
      width: 0;
      height: 0;
      border-radius: 50%;
      background-color: var(--color);
      opacity: 0; }
    [type=radio]:checked:before {
      width: 10px;
      height: 10px;
      opacity: 1; } }
